.row{
    margin-left:20px;
    color:white;
}
.row_posters{
    display:flex;
    overflow-y:hidden;
    overflow-x:scroll;
    padding:20px;
}
.row_posters::-webkit-scrollbar{
    display:none;

}
.row_poster{
    width:100%;
    object-fit:contain;
    max-height:100px;
    transition:transform 450ms;
    margin-right:10px;
}
.row_poster:hover{
    transform:scale(1.08);
    opacity:1;
}
.row_posterLarge{
    max-height: 250px;
}
.banner{
    color:white;
    object-fit:contain;
    height:448px;

}
.banner_contents{
    margin-left:30px;
    padding-top:140px;
    height:190px;
}
.banner_description{
    width:45rem;
    line-height:1.3;
    padding-top:1rem;
    font-size:0.8rem;
    max-width: 360px;
    height:80px;
}
.banner_button{
    cursor:pointer;
    color:#fff;
    outline:none;
    border:none;
    font-weight:700;
    border-radius:0.2vw;
    padding-left:2rem;
    padding-right:2rem;
    margin-right:1rem;
    padding-top:0.5rem;
    background-color: rgba(51,51,51,0.5);
    padding-bottom:0.5rem;
}

.banner_button:hover{
    color:#000;
    background-color:#e6e6e6;
    transition: all 0.2s;
}
.banner-fadeBottom{
    height:7.4rem;
    background-image: linear-gradient(180deg,transparent,
    rgba(37,37,37,0.61)
    ,#111);
}
.nav{
    position:fixed;
    top:0;
    display:flex;
    justify-content: space-between;
    width:100%;
    padding:20px;
    height:30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition:all 0.5s;
}
.nav_black{
    background-color: #111;
}
.nav_logo{
    position:fixed;
    left:20;
    width: 80px;
    object-fit: contain;
}
.nav_avatar{
    position: fixed;
    right:20px;
    width:30px;
    object-fit: contain;
    
}

body{
    padding:0;
    margin:0;
    font-family: Arial, Helvetica, sans-serif;
    
}
.logo img{
    height:80px;
    width:167px;
}
.header{
    padding:20px 50px;
    position: relative;
    z-index:10;
}
.wrap{
     /* background-image: url(https://miro.medium.com/max/1400/1*5lyavS59mazOFnb55Z6znQ.png);  */

}
.wrapper{
    background-image: url(https://miro.medium.com/max/1400/1*5lyavS59mazOFnb55Z6znQ.png);
    background-image: cover;
    background-repeat: repeat-y;
    background-size: 100%,120%;
    background-image: cover;
    background-repeat: no-repeat;
    position: relative;
    width:100vw;
    overflow: hidden; 
    height:130vh;
}
.wrapper::before{
    content:'';
    background: rgba(0,0,0,0.5);
    width:100%;
    height:100%;
    position: absolute;
    top:0;
    bottom:0;
    right:0;
}
.login_body{
    padding:60px;
    z-index:90;
    position:relative;
    max-width:450px;
    height:630px;
    margin-left: 50%;
    background: rgba(0,0,0,0.75);
    border-radius:10px;
    box-sizing: border-box;
    transform:translateX(-50%);
}
.login_body h2{
    font-size:32px;
    color:#fff;
    margin:5px 0 20px 0;
}
.login_body input{
    height:50px;
    width:100%;
    border-radius: 3px;
    background: #333;
    border:1px solid #333;
    padding-left: 15px;
    box-sizing:border-box;
    outline:none;
    color:#fff;
}
.input_wrap{
    margin-bottom:25px;
}
.login_body button{
    height:50px;
    width:100%;
    color:#fff;
    background:#e50914;
    border-radius:3px;
    font-weight:500;
    font-size:16px;
    border: 1px solid #e50914;
    margin-bottom: -10px;
}
.login_body button:hover{
    background:#f0222c;
}
.support{
    display:flex;
    color:rgb(121, 120, 120);
    justify-content: space-between;
    font-size: 13px;
}
.support input{
    height:15px;
    width:15px;
    margin-right:5px;
}

.login_facebook{
    display:flex;
    align-items: flex-end;
    width:100%;
    height:40px;
    color:#737373;
    /* background-color: aqua; */
}
.login_facebook i{
    /* width:20px;
    height:20px; */
    font-size: 20px;
    color:azure;
}
.login_facebook a{
    font-size: 14px;
}
.login_facebook span{
    margin-right:10px;
    font-size: 13px;
}
.login_facebook span a{
    text-decoration:none;
    color:#737373;
}

.sign_up{
    color:#737373;
    font-size:15px;
    width:100%;
    height:20px;
    margin:20px 0 10px 0;
}
.sign_up a{
    color:#fff;
    outline: none;
    text-decoration: none;
    margin-left: 8px;
}
.sign_up a:hover{
    text-decoration: underline;
}

.terms{
    height:25px;
    color:#737373;
    font-size: 12px;
    margin-top:20px;
}
.terms a{
    color:blue;
    margin-left:5px;
    text-decoration: none;
}


.footer{
    position: relative;
    width:100%;
    margin-top:50px;
}
.footerin{
    position: absolute;
    background-color: rgba(0,0,0,0.75);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width:100%;
    color:#737373;
    font-size: 14px;
    font-weight: 500px;
}
.foot1{
    margin:20px 0 20px 0px;
    width:55%;
}
.foot2,.foot3{
    width: 80%;
    display:flex;
    flex-direction: column;
    justify-content:center ;
    align-items: center;
}
.foot2 .inner,.foot3 .inner{
    display:flex;
    padding:10px 0;
    justify-content: space-around;
    width:80%;
}
.footerin .inner a{
    padding:5px 50px 0 0;
    text-decoration: none;
    color:#737373;
}
.footerin .inner a:hover{
    text-decoration: underline;
}
